<template>
 <van-list v-model:loading="loading" :immediate-check="false" v-model:error="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onLoad">
  <div class="swap">
    <HeadTop title="卖单市场" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="swap-item">
        <div class="title">挂卖单</div>
          <van-field
          style="margin-bottom: 12px"
    v-model="swapKttAmount"
    center
    clearable
    label=""
    @update:model-value="getOutAmount"
    placeholder="请输入卖出数量"
  >
    <template #extra>
      <span class="sympol">KTT</span>
    </template>
  </van-field>
    <van-field
    v-model="price"
    center
    clearable
    label=""
    @update:model-value="getOutAmount"
    placeholder="请输入单价"
  >
    <template #extra>
      <span class="sympol">USDT / KTT</span>
    </template>
  </van-field>
  <div class="flex-between">
    <!-- <div class="price-desc">需要： <span>{{usdtAmount}}</span> USDT</div> -->
    <div class="price-desc">余额： <span>{{kttBalance}}</span> KTT</div>
  </div>
  <div style="margin-top: -20px">
   <div class="price-desc">100KTT起步、100的整数倍挂卖</div> 
  </div>
  
  <div class="button-box">
     <van-button type="primary" :loading="btn.loading" size="large" loading-text="兑换中..." block v-if="Number(kttAllowance > 0)" @click="addBuyRecord">挂单</van-button>
     <van-button type="primary" :loading="btn.loading" v-else size="large" loading-text="正在授权..." @click="approveKtt">授权KTT</van-button>
  </div>
    </div>
    <div class="title-sub">所有挂单</div>
  
      <div class="list-box">
       <div class="item"  v-show="Number(item.kttAmount) > Number(item.haveSellAmount)" v-for="(item, index) in list" :key="index">
          <div class="top flex-middle">
            <div class="top-item flex-item">
               <div class="num">{{item.kttAmount}}</div>
               <div class="desc">卖出KTT数量</div>
            </div>
            <div class="line"></div>
            <div class="top-item flex-item">
               <div class="num yellow">{{item.price}}USDT</div>
               <div class="desc">价格/KTT</div>
            </div>
          </div>
          <div class="bottom">
            <div class="row flex-middle">
                <div class="name">挂单地址</div>
                <div class="value">{{item.buyer.substring(0, 6) + '......' + item.buyer.substring(item.buyer.length - 6, item.buyer.length)}}</div>
             </div>
             <div class="row flex-middle">
                <div class="name">已卖出数量</div>
                <div class="value">{{item.haveSellAmount}}KTT</div>
             </div>
              <div class="row flex-middle">
                <div class="name">剩余数量</div>
                <div class="value">{{item.remainAmount}}KTT</div>
             </div>
          </div>
           <div class="btn-box flex-between" v-if="Number(item.kttAmount) > Number(item.haveSellAmount)">
               <span></span>
                <van-button v-if="!item.isCancle && item.buyer == address" type="primary" class="btn-green" style="width: 120px" block @click="cancle(item.id)">取消</van-button>
                <van-button v-if="!item.isCancle" type="primary" class="btn-green" style="width: 120px" block  @click="toSell(item)">买入KTT</van-button>
                <van-button v-if="item.isCancle" type="primary" class="btn-green" style="width: 120px" block >已取消</van-button>
           </div>
       </div>
    </div>
    <van-dialog v-model:show="show" class="swap-item" :showConfirmButton="false" style="padding: 0" :title="''">
          <div class="close-box flex-between">
            <span></span>
            <van-icon @click="show=false" name="cross" color="#fff" size="20px"/>
          </div>
          <van-field
    v-model="swapAmount"
    center
    clearable
    label=""
    placeholder="请输入买入数量"
  >
    <template #extra>
      <span class="sympol">KTT</span>
    </template>
  </van-field>
  <div class="flex-between">
     <div class="price-desc">可买入: <span>{{orderInfo.remainAmount}}</span> KTT</div>
    <div class="price-desc">余额: <span>{{usdtBalance}}}</span> KTT</div>
  </div>
  
  <div class="button-box">
    <van-button color="linear-gradient(90deg, #5161F5 0%, #64C5FA 100%)" type="primary" :loading="btn.swaploading" size="large" loading-text="兑换中..." block v-if="Number(usdtAllowance) > 0" @click="sellKtt">卖出</van-button>
     <van-button color="linear-gradient(90deg, #5161F5 0%, #64C5FA 100%)" type="primary" :loading="btn.swaploading" v-else size="large" loading-text="正在授权..." @click="approveUsdt">授权KTT</van-button>
  </div>
  </van-dialog>

  </div>
     </van-list>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
import _ from 'lodash'
import marketAbi from "@/utils/marketAbi";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
import { Toast } from 'vant';
import dateformat from "dateformat"
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      marketContract: null,
      usdtContract: null,
      KttContract: null,
      usdtAllowance: '',
      usdtBalance: '0.00',
      usdtAmount: '0.00',
      kttAmount: '0.00',
      ktmAmount: '0.00',
      kttBalance: '0.00',
      kttAllowance: '',
      price: '',
       btn: {
        loading: false,
        swaploading: false
      },
      swapKttAmount: '',
      swapAmount: '',
      list: [],
      show: false,
      orderInfo: null,
      page: 1,
      limit: 15,
      loading: false,
      error: false,
      finished: false,
      initContract: () => {
        data.marketContract = new data.web3.eth.Contract(marketAbi, config.sellMarketAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.kttContract = new data.web3.eth.Contract(tokenAbi, config.kttAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getUsdtAllowance()
        data.getKttAllowance()
        data.list = []
        data.page = 1
        data.getList()
      },
        onLoad () {
        data.page++
        data.getList()
      },
      getBalance: async () => {
        let kttDecimals = await data.kttContract.methods.decimals().call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let kttBalance = await data.kttContract.methods.balanceOf(data.address).call()
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.kttBalance =  new BigNumber(kttBalance).dividedBy(10 ** kttDecimals).toFixed(2)
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** usdtDecimals).toFixed(2)
      },
      getUsdtAllowance: async () => {
        let allowance = await data.usdtContract.methods.allowance(data.address, config.sellMarketAddress).call()
        data.usdtAllowance = allowance;
      },
       getKttAllowance: async () => {
        let allowance = await data.kttContract.methods.allowance(data.address, config.sellMarketAddress).call()
        data.kttAllowance = allowance;
        console.log(allowance, '-----------allowance')
      },
      approveUsdt: async () => {
        data.btn.loading = true
        try {
          await data.usdtContract.methods.approve(config.sellMarketAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getUsdtAllowance()
          data.btn.loading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.loading = false
        }
        
      },
      approveKtt: async () => {
        data.btn.swaploading = true
        try {
          await data.kttContract.methods.approve(config.sellMarketAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getKttAllowance()
          data.btn.swaploading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.swaploading = false
        }
        
      },
      addBuyRecord: async () => {
        if(!data.swapKttAmount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.swapKttAmount) < 100) {
          Toast('数量最小100')
          return
        }
        if(Number(data.swapKttAmount) % 100 > 0) {
          Toast('必须是100的整数倍')
          return
        }
        if(!data.price) {
          Toast('请先输入价格')
          return
        }
        if(Number(data.swapKttAmount) > Number(data.kttBalance)) {
          Toast('KTT余额不足')
          return
        }
        
         data.btn.loading = true
        try {
          let decimals = await data.kttContract.methods.decimals().call()
          let usdtDecimals = await data.usdtContract.methods.decimals().call()
          let amount = new BigNumber(data.swapKttAmount).multipliedBy(10 ** decimals).toFixed()
          let price = new BigNumber(data.price).multipliedBy(10 ** usdtDecimals).toFixed()
          await data.marketContract.methods.addBuyRecord(amount, price).send({from: data.address})
          Toast('挂单成功')
          data.initData()
          data.btn.loading = false
        } catch (error) {
          Toast('挂单失败')
          data.btn.loading = false
        }
      },
      getList: async () => {
        console.log(data.page,data.limit)
        let info = await data.marketContract.methods.getUserRecords(data.page,data.limit).call({from: data.address})
        let list = info.recordArr
        console.log(info)
        let arr = []
         list.forEach((item) => {
          console.log(item.kttAmount, item.haveSellAmount, item.isCancle)
          if(Number(item.id) > 0 && !item.isCancle) {
            console.log(1111)
            arr.push({
              kttAmount:  new BigNumber(item.kttAmount).dividedBy(10**18).toFixed(2),
              haveSellAmount:  new BigNumber(item.haveSellAmount).dividedBy(10**18).toFixed(2),
              price: new BigNumber(item.price).dividedBy(10**18).toFixed(2),
              id:item.id,
              remainAmount: new BigNumber(item.kttAmount).minus(item.haveSellAmount).dividedBy(10**18).toFixed(2),
              isCancle: item.isCancle,
              buyer: item.buyer
            })
          }
        })
          if (data.page === 1) {
            data.list = arr
          } else {
            data.list.push(...arr)
          }
          data.loading = false
          data.finished = !(Math.ceil(info.total / data.limit) > data.page)
          if(!data.finished) {
            data.onLoad()
          }
      },
      toSell: (item) => {
        data.show = true
        data.orderInfo = item
      },
      sellKtt: async () => {
        if(!data.swapAmount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.swapAmount * data.orderInfo.price) > Number(data.usdtBalance)) {
          Toast('USDT余额不足')
          return
        }
         data.btn.swaploading = true
          let decimals = await data.kttContract.methods.decimals().call()
          let amount = new BigNumber(data.swapAmount).multipliedBy(10 ** decimals).toFixed()
        try {
          await data.marketContract.methods.sell(data.orderInfo.id,amount).send({from: data.address})
          Toast('买入成功')
           data.initData()
           data.show = false
          data.btn.swaploading = false
        } catch (error) {
          Toast('买入失败')
           data.show = false
          data.btn.swaploading = false
        }
      },
       cancle: async (id) => {
        try {
          await data.marketContract.methods.cancle(id).send({from: data.address})
          Toast('取消成功')
           data.initData()
        } catch (error) {
          Toast('取消失败')
        }
      },
      getOutAmount: _.throttle(async function(){
        if (!data.swapKttAmount || Number(data.swapKttAmount)  == 0 || !data.price || Number(data.price)==0) {
          return '0.00'
        }
        let usdtAmount = new BigNumber(data.swapKttAmount).multipliedBy(data.price).toFixed(2)
        data.usdtAmount = usdtAmount
      }, 1000)
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.swap{
  padding: 16px;
}
.sympol{
    font-size: 14px;
}
.swap-item{
  background: #3F2E6F;
  border-radius: 5px;
  padding: 20px 14px;
  margin-bottom: 15px;
  .title{
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  .price-desc{
    font-size: 12px;
    font-weight: 600;
    color: #E4D7FF;
    margin: 15px 8px;
    span{
      color: #FFD511;
    }
  }
}
.title-sub{
  font-size: 16px;
  font-weight: 700;
  margin: 20px 0;
  color: #fff;
}
  .list-box{
    padding-top: 20px;
    .item{
      background: #3F2E6F;
    box-shadow: 0px 4px 0px 0px #3F2E6F;
    border-radius: 15px;
    padding: 25px 20px;
    margin-bottom: 20px;
      .top{
          padding-bottom: 20px;
          border-bottom: 0.5px solid rgba(255, 255, 255, 0.1);
        .top-item{
          .num{
            color: #fff;
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 4px;
          }
          .yellow{
            color: #FFD511;
          }
          .desc{
            color: rgba(255, 255, 255, 0.7);
            font-size: 14px;
          }
        }
        .line{
          width: 0.5px;
          height: 40px;
          background: rgba(255, 255, 255, 0.1);
          margin-right: 18px;
        }
      }
      .bottom{
        padding: 18px 0 0;
        .status{
          color: #0CD3DD;
          font-size: 16px;
          font-weight: 700;
          padding-bottom: 4px;
        }
        .red{
          color: #F3489D;
        }
        .row{
          margin-top: 10px;
          .name{
            width: 110px;
            color: rgba(255, 255, 255, 0.7);
            font-size: 14px;
          }
          .value{
            color: #fff;
            font-size: 14px;
          }
        }
      }
      .btn-box{
        margin-top: 16px;
      }
    }
  }
  .close-box{
    margin-bottom: 20px;
    margin-top: -6px;
  }
</style>
